#root{
	font-family: Helvetica, sans-serif;
	display: table;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: #3d3d3d;
}

.cell{
	display: table-cell;
	vertical-align: middle;
}

.cell .content{
	margin: auto;
	background-image: url("assets/bg.png");
	background-size: cover;
	width: 600px;
	height: 400px;
	max-height: 600px;
	background-origin: content-box;
	overflow: auto;
	border: 1px solid black;
}

.input{
	color: white;
	width: 30%;
	margin-left: 20px;
	background: transparent;
	border: none;
	padding-bottom: 5px;
	border-bottom: solid 1px #007cf8;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px #39BBD7 inset !important;
}

input:-webkit-autofill {
    -webkit-text-fill-color: white !important;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #E5E3E3;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #E5E3E3;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: #E5E3E3;
}

input:focus{
	outline: none;
}

button{
	padding: 0;
	border: none;
	outline: none;
	font: inherit;
	color: white;
	background: none;
	margin-left: 20px;
	background-color: #007CF8;
	padding-top: 5px;
	padding-bottom: 5px;
	padding-left: 8px;
	padding-right: 8px;
	transition: background-color 0.5s ease;
	cursor: pointer;
}

button:hover{
	background-color: #0062f7;
}

.results{
	margin-top: 40px;
	margin-left: 20px;
	font-size: 18px;
}

.results p{
	color: white;
}

.error, .category{
	color: #f5f900 !important;
}

.error span{
	font-weight: bold;
}